import axios from 'axios';

export const getbrands = () => {
    axios.get("api/get/brands").then(response =>{
          const brands = []
          for (let i in response.data) {
            brands.push(i)
            console.log("My api value ", i)
          }  
          console.log("My api response", response.data)
          return brands
        })
      .catch(err=> {
        console.log("FETCH_BRANDS_ERROR", err)
        return err
      })
  }